.contactbackground{
    width: 100%;
    min-height: 800px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.contact-child12 {
    flex-grow: 1;
    /* background-color: antiquewhite; */
    margin: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px; /* Add gap between child elements */
}
.contact-child1,
.contact-child2 {
    flex: 1;
    margin: 0; /* Adjust margin to create spacing */
    background-color: aliceblue;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.contact-child3{
    flex-grow: 1;
    background-color: antiquewhite;
    position: relative;
    overflow: hidden;
    margin: 10px;
    border-radius: 10px;
}
.background-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover; /* Make the video cover the entire div */
}

/* .contactbackground h1{
    text-align: center;
    margin: 10px;
} */
@media screen and (max-width: 768px) {
    .contactbackground {
        flex-direction: column; /* Change flex direction to column */
    }
    .contact-child1, .contact-child2, .contact-child3{
        min-height: 400px;
    }
}